<template>
  <div id="carNoPage">
    <div class="carNoFlex">
      <div class="w30">车牌号</div>
      <van-dropdown-menu
        class="w25"
        active-color="#ff7d41"
      >
        <van-dropdown-item
          v-model="carNoL"
          :options="carNoLists"
        />
      </van-dropdown-menu>
      <van-field
        v-model="carNoR"
        maxlength='7'
        placeholder="请输入车牌号"
      />
    </div>
    <div class='f0'>更换车牌后，前面录入的车辆会按实际收费计费</div>
    <van-button
      class="btnbg"
      @click="onCommit"
    >提交车牌</van-button>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { PostBusinessOrderroomCar } from '@/api/api_business_hotel_and_scale';
import carNoLists from '../../../../static/js/carNoList';
export default {
    components: { carNoLists },
    data() {
        return {
            carNoLists: {},
            aaa: true,
            carNoL: 23,
            carNoR: null
        };
    },
    created() {
        this.carNoLists = carNoLists; // 初始化选择器数据
        if (this.$route.query.carNo) {
            this.carNoL = this.carNoLists.find((item) => this.$route.query.carNo.substr(0, 1) == item.text).value;
            this.carNoR = this.$route.query.carNo.substr(1);
        }
        this.$store.commit('setHeader', '录入车牌');
    },
    methods: {
        onCommit() {
            if (!this.carNoR) {
                Toast('请输入车牌号');
            } else {
                let number = this.isVehicleNumber(this.carNoLists.find((item) => this.carNoL == item.value).text + this.carNoR);
                if (number == true) {
                    let data = {
                        carNo: (this.carNoLists.find((item) => this.carNoL == item.value).text + this.carNoR).toUpperCase()
                    };
                    PostBusinessOrderroomCar(this.$route.query.orderRoomId, data).done((res) => {
                        Toast(res.msg);
                        setTimeout('window.history.go(-1)', 900);
                    });
                } else {
                    Toast('请正确输入车牌号');
                }
            }
        },
        isVehicleNumber(vehicleNumber) {
            vehicleNumber = vehicleNumber.toUpperCase();
            var xreg = /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}(([0-9]{5}[DF]$)|([DF][A-HJ-NP-Z0-9][0-9]{4}$))/;
            var creg = /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9挂学警港澳]{1}$/;
            if (vehicleNumber.length == 7) {
                return creg.test(vehicleNumber);
            } else if (vehicleNumber.length == 8) {
                return xreg.test(vehicleNumber);
            } else {
                return false;
            }
        }
    }
};
</script>
<style scoped>
#carNoPage {
    font-size: 14px;
}
.carNoFlex {
    display: flex;
    align-items: center;
    margin-top: 20px;
    background: #fff;
}
.w25 {
    width: 25%;
}
.w30 {
    width: 30%;
    text-align: center;
}
.f0 {
    color: #ff5257;
    font-size: 12px;
    text-align: left;
    margin: 12px 0 30px 14px;
}
.btnbg {
    background: #ff7d41;
    color: #fff;
    width: 95%;
    margin: 0 8px;
    border-radius: 5px;
}
/deep/ .van-dropdown-menu__title {
    color: #000;
}
/deep/ .van-popup--top {
    top: 0px;
    left: 88px;
    width: 25%;
}
/deep/ .van-dropdown-menu__bar {
    box-shadow: none;
}
</style>



